export default {
  layout: 'admin',
  data() {
    return {
      results: [],
      listLoading: false,
      page: {},
      filters: {},
      tagOptions: []
    }
  },
  mounted() {
    this.recent()
  },
  methods: {
    async list() {
      this.listLoading = true
      const params = Object.assign(this.filters, {
        page: this.page.page,
        limit: this.page.limit
      })
      try {
        const data = await this.$axios.post('/api/admin/article/list', params)
        this.results = data.results
        this.page = data.page
      } catch (err) {
        this.$message.error(err.message)
      } finally {
        this.listLoading = false
      }
    },
    async recent() {
      this.listLoading = true
      try {
        this.results = await this.$axios.get('/api/admin/article/recent')
      } catch (err) {
        this.$message.error(err.message)
      } finally {
        this.listLoading = false
      }
    },
    handlePageChange(val) {
      this.page.page = val
      this.list()
    },
    handleLimitChange(val) {
      this.page.limit = val
      this.list()
    },
    deleteSubmit(row) {
      const me = this
      this.$confirm('确认要删除文章？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          this.$axios
            .post('/api/admin/article/delete', { id: row.id })
            .then((data) => {
              me.$message({ message: '删除成功', type: 'success' })
              me.list()
            })
            .catch((rsp) => {
              me.$notify.error({ title: '错误', message: rsp.message })
            })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
    },
    pendingSubmit(row) {
      const me = this
      this.$confirm('确认要过审文章？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          this.$axios
            .post('/api/admin/article/pending', { id: row.id })
            .then((data) => {
              me.$message({ message: '审核成功', type: 'success' })
              me.list()
            })
            .catch((rsp) => {
              me.$notify.error({ title: '错误', message: rsp.message })
            })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消审核'
          })
        })
    }
  }
}
